.slick-slider {
  margin-bottom: 100px;
}

.slick-center .slick-image {
  width: 290px;
  height: 390px;
  background: #ffffff;
  box-shadow: 0px 4px 27px rgba(0, 0, 0, 0.15);
}

.slick-center .slick-name {
  display: block;
}
.slick-center .slick-description {
  display: block;
}

.slick-center .slick-cost {
  display: block;
}

.slick-slide .slick-active .slick-cloned {
  width: 100%;
}

/* .slick-initialized .slick-slide {
  width: !important;
} */

.slick-wrapper {
  /* width: 300px; */
  padding: 0;
}

.slick-image {
  width: 240px;
  height: 310px;
  background: rgb(215, 184, 184);
  margin-bottom: 25px;
}

.slick-name {
  display: none;
  text-align: center;
  margin-bottom: 15px;
  max-width: 400px;
  width: 100%;
}
.slick-description {
  display: none;
}

.slick-cost {
  display: none;
}

.slick-dots {
  /* margin-bottom: 100px; */
}

.slick-dots .slick-active p {
  color: #ffc800;
}

.slick-next {
  display: none;
  opacity: 0;
  background-color: rebeccapurple;
}

.container {
  position: relative;
  overflow: hidden;
}

.slider {
  display: flex;
  position: relative;
  align-items: center;
}

.prew {
  position: absolute;
  top: 43%;
  padding: 15px;
  background-color: green;
  left: 0;
}

.next {
  position: absolute;
  top: 43%;
  padding: 15px;
  background-color: green;
  right: 0;
}

.item-slider.active {
  animation: fadein 1s;
}

@keyframes fadein {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
